<template>
    <div class="home-page">
        <template v-for="(item, index) in children" :key="item.name">
            <div class="home-card" @click="topage(item.path)" v-if="item?.meta?.hidden">
                <img :src="imgList[index - 1]" alt="图片不见啦" />
                <h2>{{ item?.meta?.title || '标题' }}</h2>
            </div>
        </template>
    </div>
</template>

<script setup lang="ts">
import { reactive, ref } from 'vue'
import { useRoute, useRouter } from 'vue-router'
let imgList = ref<string[]>([])
for (let index = 0; index < 9; index++) {
    imgList.value.push(new URL(`../../static/homeImg/${index + 1}.gif`, import.meta.url).href)
}
/**
 * 路由对象
 */
const route = useRoute()
/**
 * 路由实例
 */
const router = useRouter()
/**
 * 数据部分
 */

const children = route.matched[0].children

/**
 * 跳转页面
 *
 */
const topage = (path: string) => {
    router.push(path)
}
